	import Helper from '@/function.js';
	export default {
		data() {
			return {
				token:"",
				info:{},
				payType:0,
			}


		},
		onLoad:function(options){
			//this.token = uni.getStorageSync('token');
			//this.info = JSON.parse(options.item);
		},
		
		methods: {
			btnClick(val){
				this.payType = val;
			},
			payOrder(){
				uni.showLoading({
					title:"正在下单..."
				})
				var _this = this;
				uni.request({
					url: _this.ApiGateWay + "/sale/Member/PayTaskPackage?taskPackageId="+_this.info.id + "&payType="+_this.payType,
					data:"",
					header: {
						Authorization: _this.token
					},
					method: 'POST',
					dataType: 'json',
					success: function(res) {
				
						if (Helper.CheckData(res)) {
							var qrcode = res.data.data;
							
							uni.showModal({
								title:"是否支付完成了",
								content:"为了避免订单超时,请尽快完成支付",
								confirmText:"已完成",
								success:function(res){
									if (res.confirm){
										uni.navigateTo({
											url:"/pages/my/release"
										})
									}
									else
									{
										uni.switchTab({
											url:"/pages/live/live"
										})
									}
								}
							})
							
							var url = navigator.userAgent;
							if (url.indexOf("cdridge-android") > 0) {				
								wv.goUrl(qrcode)
							} else {
								location.href = qrcode;
							}
							
							
							//判断环境---跳支付宝链接
							//



							
							//goUrl
							



						}
				
					},
					complete() {
						uni.hideLoading();
					},
					fail: function(res) {
						uni.stopPullDownRefresh();
					}
				});
			}
			// bank() {
   //             uni.navigateBack({
   //             	delta:1
   //             })
			// }

		}

	}
